<template>
  <a-layout  :style="{ background: '#fff', padding: 0 }">
    <a-layout-header :style="{ background: '#fff', padding: 0 }" />
    
    <a-layout :style="{ marginLeft: '200px',background: '#fff'}">
      <a-layout-sider theme="light" :style="{ overflow: 'auto', height: '100vh', position: 'fixed', left: 0 }">
        <a-menu
          :default-open-keys="openKeys"
          mode="inline"
        >
          <a-sub-menu v-for="v in menu" :key="v.key">
            <span slot="title"><span>{{v.title}}</span></span>
            <a-menu-item v-for="v1 in v.children" :key="v1.key" @click="pageTo(v1.name)">
              {{v1.title}}
            </a-menu-item>
          </a-sub-menu>
        </a-menu>
      </a-layout-sider>
      <a-layout>
        <a-layout-content  :style="{ padding: '24px', overflow: 'initial' ,background: '#fff',}">
          <router-view/>
        </a-layout-content>
        <a-layout-footer :style="{ textAlign: 'center' }">
          Osff Design ©2020 
        </a-layout-footer>
      </a-layout>
    </a-layout>
    
  </a-layout>
</template>


<script>
export default {
  data(){
    return {
      menu:[],
      openKeys:[],
    }
  },
  created(){
    this.menu = this.$router.options.routes
    this.init()
  },
  methods:{
    init(){
      this.menu.forEach(element => {
        this.openKeys.push(element.key)
      });
      console.log(this.openKeys)
    },
    pageTo(name){
      this.$router.push({name:name})
    }
  }
}
</script>