import Layout from '@/views/Home.vue'
function createRandomId() {
    return (Math.random()*10000000).toString(16).substr(0,4)+'-'+(new Date()).getTime()+'-'+Math.random().toString().substr(2,5);
}
const routes = [
    {
        path: '/',
        name: '',
        title:'开发指南',
        component: Layout,
        key:createRandomId(),
        // meta:{},
        children:[
            {
                path: '/',
                name: 'introduction',
                title:'介绍',
                key:createRandomId(),
                component: () => import('@/components/introduction/introduction.vue'), 
            },
            {
                path: '/quickstart',
                name: 'quickstart',
                title:'快速上手',
                key:createRandomId(),
                component: () => import('@/components/quickstart/quickstart.vue'), 
            }
        ]
    },
    {
        path: '/Base',
        name: 'Base',
        title:'基础模板',
        component: Layout,
        key:createRandomId(),
        // meta:{},
        children:[
            {
                path: '/icon',
                name: 'icon',
                title:'Icon图标',
                key:createRandomId(),
                component: () => import('@/components/icon/icon.vue'),
                
            },
            {
                path: '/button',
                name: 'button',
                title:'Button按钮',
                key:createRandomId(),
                component: () => import('@/components/button/button.vue'),
                
            }
        ]
    },
    {
        path: '/Display',
        name: 'Display',
        title:'展示模板',
        component: Layout,
        key:createRandomId(),
        // meta:{},
        children:[
            {
                path: '/carousels',
                name: 'carousels',
                title:'轮播图',
                key:createRandomId(),
                component: () => import('@/components/carousels/carousels.vue'),
                
            }
        ]
    },
    
  ]


  export default routes